.footer {
  &__content {
    background: #dfdfdf;
    padding: 60px 0 80px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__info {
    width: 540px;
  }
  &__info-title {
    color: $title;
    @extend %title-font;
    margin-bottom: 14px;
    line-height: 23px;
  }

  &__info-text {
    margin-bottom: 16px;
    max-width: 475px;
  }

  &__info-email {
    font-size: 14px;
    color: $title;
    font-weight: 700;

    padding-left: 30px;
    margin-bottom: 22px;
    display: inline-block;
    position: relative;
    &::before {
      content: "";
      left: 0;
      top: 9px;
      width: 16px;
      height: 12px;
      position: absolute;
      background-repeat: no-repeat;
      background-image: url(../images/mail.svg);
    }
  }
  &__form {
    display: flex;
  }
  &__form-input {
    padding: 6px 10px 6px 18px;
    border-radius: 21px;
    background-color: transparent;
    border: 1px solid rgba($title, 0.7);
    outline: none;
    width: 100%;
    &::placeholder {
      font-size: 16px;
      line-height: 27;
      color: #a1a1a1;
    }
  }

  &__form-button {
    background-color: $title;
    color: #ffff;
    border: none;
    border-radius: 21px;
    font-weight: 400;
    padding: 6px 12px;
    min-width: 160px;
    min-height: 39px;
    margin-left: 9px;
    cursor: pointer;
  }

  &__social {
    width: 125px;
    margin-right: auto;
    margin-left: 100px;
  }
  &__social-link {
    padding-left: 25px;
    position: relative;
    &::before {
      top: 3px;
      content: "";
      position: absolute;
      left: 0;
      width: 16px;
      height: 16px;
      background-repeat: no-repeat;
    }
  }
  &__social-link--facebook::before {
    background-image: url(../images/facebook.svg);
    background-repeat: no-repeat;
  }
  &__social-link--instagram::before {
    background-image: url(../images/instagram.svg);
    background-repeat: no-repeat;
    width: 14px;
    height: 14px;
    top: 4px;
  }
  &__social-link--pinterest::before {
    background-image: url(../images/pinterest.svg);
    background-repeat: no-repeat;
  }
  &__social-link--whatsapp::before {
    background-image: url(../images/whatsapp.svg);
    background-repeat: no-repeat;
    width: 14px;
    height: 14px;
  }
  &__social-link--youtube::before {
    background-image: url(../images/youtube.svg);
    background-repeat: no-repeat;
    top: 4px;
    width: 18px;
    height: 12px;
  }
  &__social-item,
  &__menu-item {
    margin-bottom: 15px;
    font-weight: 600;
    font-size: 14px;
    color: #707070;
    font-family: "Open Sans", sans-serif;
    font-style: normal;
  }

  &__social-link,
  &__menu-link {
    color: #707070;
  }
  &__menu {
    margin-right: 60px;
    width: 132px;
  }

  &__copyright {
    background-color: $title;
    color: #a1a1a1;
    text-align: center;
    padding: 42px 0 41px;
  }
}
