.gallery{
   padding: 80px 0 90px;
   &__buttons{
      text-align: center;
      margin-bottom: 40px;
   }
   &__btn{
      margin: 0 13px;
      background-color: transparent;
      border-radius: 20px;
      border: 1px solid rgba(#454647, .5);
      padding: 6px 16px;
      color: #8F8D8D;
      font-size: 14px;
      font-weight: 600;
      cursor: pointer;
   }
   .mixitup-control-active{
      background-color: rgba(#FFEF35, .5);
      color: $title;
      border: 1px solid #FFEF35;
   }
   &__items{
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: repeat(2, 1fr);
      grid-gap: 30px 35px ;
   }
   &__item:first-child{
      grid-column: span 2;
      grid-row: span 2;
   }
   &__item img{
      object-fit: cover;
      width: 100%;
      max-height: 1150px;
      height: 100%;
   }
}