.decor {
  padding: 170px 0 155px;
  background-image: url(../../../build/images/decor-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  &__title {
    max-width: 730px;
    color: $title;
    font-size: 60px;
    line-height: 80px;
    padding-bottom: 22px;
    margin-bottom: 22px;
    position: relative;
    @extend %title-font;
    &::after {
      content: "";
      position: absolute;
      height: 1px;
      width: 33%;
      left: 0;
      bottom: 0;
      background: #979797;
    }
  }
  &__text {
    max-width: 540px;
  }
}

