.top {
   &__inner{
      padding-bottom: 70px;
      position: relative;
      &::after{
         content: "";
         background-image: url(../../../build/images/slider-bg.jpg);
         width: 100%;
         position: absolute;
         height: 722px;
         top: 0;
         z-index: -1;
      }
   }
&__slider-item{
   display: flex !important; // !important задает display flex первоначальным,а не inline block от слик слайдера
   justify-content: center;
   align-items: center;
}
&__slider-img{
   margin-left: -14%;
   max-width: 703px;
   height: 815px;
   object-fit: cover;
}
&__slider-info{
   max-width: 645px;
   margin-left: 25px;
}
&__slider-title{
   color: $title;
   font-size: 110px;
   line-height: 135px;
   padding-bottom: 26px;
   margin-bottom: 26px;
   position: relative;
   @extend %title-font;
   &::after{
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 185px;
      height: 1px;
      background: #6D7278;
   }
   &__slider-text{
      max-width: 585px;
   }
}
}